export default {
	data() {
		return {
			todaytime:'',
			isactive:1,
			show:false,
			mode:'date',
			id:0, // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../../static/location.png'
				}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../../static/location.png'
			}],
			lon:'',
			lat:'',
			exper_id:0,
			userinfo:[],
			datalist:[],
			name:'',
			phone:'',
			usernum:1,
			userimg:'',
			sbimg:'../../static/u1530.png',
			// type:1,
			makeapp:0,
			maketime:'2020-02-02',
			choicetime:'请选择'
		}
	},
	onShow(options) {
		uni.getStorage({
			key:'choice_time',
			success:(res)=>{
				console.log(res.data)
				this.choicetime = res.data
				setTimeout(()=>{
					uni.removeStorage({
					    key: 'choice_time',
					    success: function (res) {
					        console.log('success');
					    }
					});
				},1000000)
				
			},fail() {
				this.choicetime = '请选择'
			}
		})
		console.log('嘿嘿')
	},
	onLoad(options) {
		
		console.log(options)
		options.id = 5
		this.exper_id = options.id
		this.getTime()
		this.getdata()
	},
	methods: {
		getTime(){
		
			var date = new Date(),
			year = date.getFullYear(),
			month = date.getMonth() + 1,
			day = date.getDate(),
			hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
			minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
			second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			month >= 1 && month <= 9 ? (month = "0" + month) : "";
			day >= 0 && day <= 9 ? (day = "0" + day) : "";
			var timer = year + '-' + month + '-' + day ;
			this.todaytime = timer
			// return timer;
		},
		aoligei(e){
			this.choicetime = e
		},
		upimg(){
			let _self = this
			uni.chooseImage({
			    count: 1,
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
					 const tempFilePaths = res.tempFilePaths;
					 uni.uploadFile({
						url : _self.common.websiteUrl+"service_uploadimg",
						header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
						filePath: tempFilePaths[0],
						name: 'images',
						formData: {
						    'compress': true
						},
						success: (res)=> {
							var res = JSON.parse(res.data)
							console.log(res)
							_self.userimg = res.data
							_self.sbimg = _self.common.getimgurl(res.data)
							_self.common.callback('上传成功')
						},fail(){
							_self.common.network()
						}
					 });
			    },error : function(e){
			     console.log(e);
			    }
			});
		},
		select(index){
			this.isactive = index
		},
		back(){
			uni.navigateBack({
			    delta: 1
			});
		},
		setusernum(e){
			this.usernum = e
		},
		qr(){
			let _self = this
			if(_self.name==''){
				return _self.common.callback('请输入姓名')
			}
			
			var reg=/^1[3456789]\d{9}$/;
			if(!reg.test(_self.phone)){
				return _self.common.callback('请输入有效的手机号码');
			}
			
			if(_self.userimg==''){
				return _self.common.callback('请上传本人照片')
			}
			
			if(_self.choicetime=='未选择'){
				return _self.common.callback('请选择预约时间')
			}
			uni.request({
				url:_self.common.websiteUrl+"experhome_owners_makeapp",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
						'user_id':_self.userinfo.id,
						'exper_id':_self.exper_id,
						'name':_self.name,
						'phone':_self.phone,
						'makenum':_self.usernum,
						'maketype':_self.isactive,
						'makeapp':_self.datalist.makeapp,
						'userimg':_self.userimg,
						'maketime':_self.choicetime,
						// 'name':_self.name,
					},
				success: (res) => {
					if(res.data.code==200){
						_self.common.callback('预约成功')
						setTimeout(() => {
							_self.back()
						}, 2000);
					}
				},fail() {
					_self.common.network()
				}
			});
			
			
		},
		get_loacal(){
			return new Promise((resolve,reject)=>{
				
				uni.getLocation({
					type: 'wgs84',
					geocode:true,//设置该参数为true可直接获取经纬度及城市信息
					success:(res) =>{
						this.lon = res.longitude
						this.lat = res.latitude
						console.log(this.lon)
						resolve('suc')
					},
					fail: function () {
						uni.showToast({
							title: '获取地址失败，将导致部分功能不可用',
							icon:'none'
						});
						resolve('err')
					}
				});
				uni.getStorage({
					key: 'userinfo',
					success:(res)=>{
						this.userinfo = res.data
					}
				});	
			})
		},
		async getdata(){
			
			var that = this
			await that.get_loacal()
			
			uni.request({
				url:that.common.websiteUrl+"experhome_owners_exper_def",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
						'user_id':that.userinfo.id,
						'exper_id':that.exper_id,
						'lon':that.lon,
						'lat':that.lat
					},
				success: (res) => {
					that.datalist = res.data.data
					console.log(that.datalist)
				},fail() {
					that.common.network()
				}
			});
		
			
			
		},
		showtime(){
			uni.navigateTo({
			    url: '../calendar/calendar?id='+this.exper_id,
				animationType: 'fade-in',
				animationDuration: 200
			});
		},
		names(){
			uni.navigateTo({
			    url: '../namecertification/namecertification'
			});
		}
	}
}
